// @flow

import React from 'react'
import {Hits, Index} from 'react-instantsearch-dom'

import PageHit from '../page-hit'
import HitCount from '../hit-count'

import type {Props} from './types'

const Main: (props: Props) => React$Node = (props: Props) => {
  const {
    index: {name},
  } = props

  return (
    <Index indexName={name}>
      <HitCount />
      <Hits className="Hits" hitComponent={PageHit} />
    </Index>
  )
}

export default Main
