// @flow

import styled, {css} from 'styled-components'
import {Toolbar} from '@material-ui/core'
import {Link} from 'gatsby'

import {SearchBox, SearchResult} from '../components'

export const MyToolbar = styled(Toolbar)`
  border-bottom: 1px solid ${props => props.theme.palette.divider};
`

export const SecondaryToolbar = styled(Toolbar)`
  justify-content: space-between;
  overflow-x: auto;
`

export const HeaderLink = styled(Link)`
  flex: 1;
  text-decoration: none;
  color: inherit;
`

export const MyLink = styled(Link)`
  padding: 8px;
  flex-shrink: 0;
  color: inherit;
  text-decoration: none;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin: 0;
  box-sizing: inherit;

  &:hover {
    text-decoration: underline;
  }
`

const OPEN: string = css`
  width: 10em;
  background: ${({theme}) => theme.background};
  cursor: text;
  margin-left: -1.6em;
  padding-left: 1.6em;
`

const CLOSED: string = css`
  width: 0;
  background: transparent;
  cursor: pointer;
  margin-left: -1em;
  padding-left: 1em;
`

export const StyledSearchRoot: typeof styled.div = styled.div`
  position: relative;
  margin: 0.6em 0;
`

export const StyledSearchBox: typeof SearchBox = styled(SearchBox)`
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  margin-bottom: 0;

  .SearchInput {
    outline: none;
    border: ${({hasFocus}) => (hasFocus ? 'auto' : 'none')};
    font-size: 1em;
    transition: 100ms;
    border-radius: 2px;
    color: ${({theme}) => theme.foreground};
    ::placeholder {
      color: ${({theme}) => theme.faded};
    }
    ${({hasFocus}) => (hasFocus ? OPEN : CLOSED)}
  }

  .SearchIcon {
    width: 1em;
    margin: 0.3em;
    color: ${({theme}) => theme.foreground};
    pointer-events: none;
  }
`

const POPVER: string = css`
  max-height: 80vh;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  position: absolute;
  z-index: 2;
  right: 0;
  top: 100%;
  margin-top: 0.5em;
  width: 80vw;
  max-width: 30em;
  box-shadow: 0 0 5px 0;
  padding: 1em;
  border-radius: 2px;
  background: ${({theme}) => theme.background};
`
export const StyledSearchResult: typeof SearchResult = styled(SearchResult)`
  display: ${props => (props.show ? `block` : `none`)};
  ${POPVER}
  .HitCount {
    display: flex;
    justify-content: flex-end;
  }
  .Hits {
    ul {
      list-style: none;
      margin-left: 0;
    }
    li.ais-Hits-item {
      margin-bottom: 1em;
      a {
        color: ${({theme}) => theme.foreground};
        h4 {
          margin-bottom: 0.2em;
        }
      }
    }
  }
  .ais-PoweredBy {
    display: flex;
    justify-content: flex-end;
    font-size: 80%;
    svg {
      width: 70px;
    }
  }
`
