// @flow
import React from 'react'
import {makeStyles} from '@material-ui/core/styles'
import {Drawer, List, ListItem, ListItemIcon, ListItemText} from '@material-ui/core'
import {MoveToInbox as InboxIcon} from '@material-ui/icons'
import {Link} from 'gatsby'

import type {Props, MenuItem} from './types'

const useStyles = makeStyles({
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
})

const menus: Array<MenuItem> = [
  {name: 'Downloader', path: '/downloader'},
  {name: 'Image Uploader', path: '/image-uploader'},
  {name: 'File Uploader', path: '/file-uploader'},
  {name: 'File Splitter', path: '/file-splitter'},
  {name: 'Cookie Refresher', path: '/cookie-refresher'},
]

const NavigationDrawer = (props: Props) => {
  const {
    onClose,
    open,
    location: {pathname},
  } = props

  const classes = useStyles()

  // TODO check auth

  return (
    <Drawer anchor="left" open={open} onClose={onClose}>
      <div className={classes.list} role="presentation" onClick={onClose} onKeyDown={onClose}>
        <List>
          {menus.map(({path, name}) => {
            return (
              <ListItem button key={path} component={Link} to={path} selected={pathname === path}>
                <ListItemIcon>
                  <InboxIcon />
                </ListItemIcon>
                <ListItemText primary={name} />
              </ListItem>
            )
          })}
        </List>
      </div>
    </Drawer>
  )
}

export default NavigationDrawer
